import {Link, Outlet} from 'react-router-dom'
import Header from './layout/Header'
import Footer from './layout/Footer'

import ReactFullpage from "@fullpage/react-fullpage"
import "../fullpage.min.css"
import "../fullpage_custom.css"

export default function Main(){
  return(
    <div>
      <Header/>

      <ReactFullpage
        scrollOverflow={true}
        navigation={true}
        scrollingSpeed={1200}
        //sectionsColor={["orange", "purple", "green"]}
        render={({ state, fullpageApi }) => {
          return (
            <div id="fullpage-wrapper">
              <div className="section section1">
                <div className="box-wrap">
                  <div className="inner-wrap">
                    <h1 style={{display:"none"}}></h1>
                    <div className="container">
                      <h1>COMING SOON</h1>
                      <div className="bar">
                        <div className="progress"></div>
                      </div>
                      <h1 style={{position:'absolute',bottom:'0',left:'50%',transform:'translate(-50%, -80px)',fontSize:'20px'}}>SCROLL<br/>DOWN</h1>
                    </div>
                  </div>
                </div>
              </div>
              <div className="section">
                <div className="slide">
                  <h3>2.1</h3>
                </div>
                <div className="slide">
                  <h3>2.2</h3>
                </div>
                <div className="slide">
                  <h3>2.3</h3>
                </div>
              </div>
              <div className="section">
                <h3>3</h3>
                <button onClick={() => fullpageApi.moveTo(1, 0)}>Move top</button>
              </div>
            </div>
          )
        }}
      />
      
      <Outlet/>
      <Footer/>
    </div>
  )
}